.spy-background {
  position: fixed;
  bottom: 0;
  right: 0;
}
.spy-background h1 {
  font-size: 13em;
  font-weight: 300;
  margin: 0;
  margin-top: 50px;
  margin-bottom: 50px;
  margin-right: 100px;
  color: rgba(0, 0, 0, 0.05);
  z-index: -1;
  user-select: none;
}

.floating {
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes floating {
  0% {
    transform: translate(0, -25px);
  }
  50% {
    transform: translate(0, 25px);
  }
  100% {
    transform: translate(0, -25px);
  }
}

